/* with custom styles */
@charset 'utf-8';
body.dark-theme {
  & .tui-pagination .tui-page-btn,
  & .tui-pagination .tui-ico-ellip {
    background-color: $grayColor;
    color: $whiteTextColor;
  }
  & .tui-pagination .tui-page-btn:hover,
  & .tui-pagination .tui-prev,
  & .tui-pagination .tui-next,
  & .tui-pagination .tui-is-disabled:hover {
    background-color: $modalInfoColor;
  }
  .tui-pagination .tui-is-selected,
  .tui-pagination strong {
    color: $whiteTextColor;
    background-color: $heartColor;
    cursor: default;
  }
}
.pagination {
  margin-bottom: 40px;
  @media screen and (min-width: 767px) {
    margin-bottom: 60px;
  }
}

.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.tui-pagination > .tui-page-btn.tui-first,
.tui-pagination > .tui-page-btn.tui-last {
  display: none;
}

.tui-pagination > .tui-page-btn.tui-prev-is-ellip,
.tui-pagination > .tui-page-btn.tui-next-is-ellip {
  @media screen and (max-width: 767px) {
    display: none;
  }
}

.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  height: auto;
  margin: 14px 0 12px;
  line-height: normal;
  text-align: center;
  font-size: 0;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 13px 0;
  margin-left: 0;
  color: $blackTextColor;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.333;
  text-decoration: none;
  vertical-align: middle;
  border-radius: $borderRadius;
  border: none;
}

.tui-pagination .tui-page-btn:hover {
  background-color: $footerBackground;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: $whiteTextColor;
  background-color: $heartColor;
  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  background-color: $heartColor;
}

.tui-pagination .tui-prev,
.tui-pagination .tui-next {
  background-color: $footerBackground;
}

.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
  background-color: $footerBackground;
  cursor: default;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}
